import React, { useContext, useEffect, useState } from "react";
import PageTitle from "../../Container/PageTitle";
import { authContext } from "../../../WRAPPERS/Context/myContext";
import useHttpClient from "../../../hooks/useHttpClient";
import "./blogpage.css";
import Grid from "@material-ui/core/Grid";
import Container from "../../Container/container";
import BlogNav from "./nav/BlogNav";
import MainFeaturedPost from "./nav/MainFeaturedPost";
import FeaturedPost from "./FeaturedPosts";
import { Route, useRouteMatch } from "react-router-dom";
import Post from './post'
import PostsAsCategory from "./postsAsCategory";

const BlogPage = () => {
  const { isLoading, sendRequest } = useHttpClient();
  const [categories, setCategories] = useState([]);
  const auth = useContext(authContext);
  const route = useRouteMatch();
  const mainFeaturedPost = {
    title: "Ä°zmir'in Ãœlkemizdeki En YaÅŸanÄ±lasÄ± Åžehir OlduÄŸunun 24 KanÄ±tÄ±",
    description:
      "Herkesin aklÄ±nda gerÃ§ekleÅŸtirmek iÃ§in yanÄ±p tutuÅŸtuÄŸu bir hayal vardÄ±r: TasÄ± taraÄŸÄ± toplayÄ±p Ä°zmir'e yerleÅŸmek. Kimileri baÅŸarÄ±r bunu, kimileri ise asla imkan bulamaz. Bu noktada en ÅŸanslÄ± olanlar, halihazÄ±rda Ä°zmir'de yaÅŸayan kiÅŸilerdir Ã§Ã¼nkÃ¼ onlar bilir ki, Ä°zmir'i harika bir ÅŸehir haline getiren onlarca Ã¶zelliÄŸi var.",
    image:
      "https://ipektatil.com/wp-content/uploads/2017/12/5a1ec2540f25440f288d63a5.jpg",
    imgText: "Ä°zmir gece resmi",
    linkText: "DevamÄ±nÄ± okuâ€¦",
  };

  const [featuredPosts, setfeaturedPosts] = useState([]);

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const res = await sendRequest(
          process.env.REACT_APP_BACKEND_URL + "/blog/categories"
        );
        setCategories(res.categories);
      } catch (error) {}
    };
    fetchCategories();
  }, []);

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const res = await sendRequest(process.env.REACT_APP_BACKEND_URL + "/blog");
        setfeaturedPosts(res.posts);
      } catch (error) {}
    };
    fetchCategories();
  }, []);

  return (
    !isLoading &&
    categories.length > 0 &&
    featuredPosts.length > 0 && (
      <div>
        <PageTitle>Blog</PageTitle>
        <BlogNav isLoding={isLoading} categories={categories} route={route} />
        <Route exact path="/Blog">
          <MainFeaturedPost post={mainFeaturedPost} />
          <Container>
            <Grid container spacing={4}>
              {featuredPosts.map((post) => (
                <FeaturedPost
                  route={route.path}
                  image={post.imageUrl}
                  key={post._id}
                  post={post}
                />
              ))}
            </Grid>
          </Container>
        </Route>
        {categories.map((c) => (
          <Route key={c._id} path={`${route.path}/kategori/${c.label}`}>
            <PostsAsCategory route={route} categoryId={c._id}/>
          </Route>
        ))}
        {featuredPosts.map((post) => (
          <Route key={post._id} path={`${route.path}/postId/${post._id}`}>
            <Post post={post}/>
          </Route>
        ))}
      </div>
    )
  );
};

export default BlogPage;
